<template>
    <div>
        <div class="jsModal" v-show="autoTarget">
            <div class="c-overlay">
                <div class="c-overlay__contents">
                    <div class="c-overlay__ttl">{{ autoTarget }}</div>
                    <div class="c-overlay__description"><span class="u-red">*</span><br class="u-sp_br"></div>
                    <div class="c-overlay__btnContainer c-overlay__btnContainer--auto">
                        <div class="c-search">
                            <div class="c-search__keywords">
                                <nav class="c-solidMenu">
                                    <ul>
                                        <li>
                                            <div class="c-solidMenu__date">
                                                <vue-ctk-date-time-picker class="overlay-date-time-picker" v-model="dateValue" label="">
                                                </vue-ctk-date-time-picker>
                                                <a class="c-solidMenu__date__a" href="javascript:void(0)"
                                                    id="cookiesDom">
                                                    <textarea placeholder="tweet"
                                                        v-model="tweetText" id="tweet_textarea" class="c-solidMenu__textarea" name=""
                                                        cols="30" rows="8"></textarea>
                                                </a>
                                            </div>
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                        </div>
                        <button class="c-appBtn" v-on:click="autoTweet()"></button>
                        <button v-show="!auto_tweet_flg" class="c-appBtn" v-on:click="autoTweetOn()">tweet ON</button>
                        <button v-show="auto_tweet_flg" class="c-appBtn" v-on:click="autoTweetStop()">tweet
                            OFF</button>
                        <button class="c-appBtn" v-on:click="autoCancel()"></button>
                    </div>
                </div>
            </div>
        </div>
        <div class="c-appBtn"><a class="c-appBtn--none" id="js-tweet-btn" :class="{'c-appBtn--auto': auto_tweet_flg}"
                v-on:click="autoAction('')"><span v-if="auto_tweet_flg"></span><span
                    v-else></span></a></div>
    </div>
</template>

<script>
export default {
    props: ['user_id', 'auth_screen_name', 'auto_tweet_flg'],
    data: function () {
        return {
            autoTarget: '',
            add_keyword: 0,
            keywords: '',
            cookiesData: '',
            tweetText: '',
            dateValue: '',
        }
    },
    methods: {
        autoAction: function (targetName) {
            this.autoTarget = targetName;
        },
        autoCancel: function () {
            this.autoTarget = '';
            var url = $(location).attr('href');
            if (url.indexOf("twitter/tweetList") >= 0 && this.add_keyword > 0) {
                window.location.reload(false)
            } 
        },
        autoTweet: async function () {
            if ((this.tweetText.length <= 140) && (this.tweetText.length > 0)) {
                const formData = new FormData();
                formData.append('user_id', this.user_id);
                formData.append('screen_name', this.auth_screen_name);
                formData.append('tweet_text', this.tweetText);
                formData.append('date_value', this.dateValue);

                await this.$axios.post('/api/twitter/autoTweet', formData)
                    .then((res) => {
                        this.add_keyword = this.add_keyword + 1;
                        this.dateValue = '';
                        this.tweetText = '';
                        alert('tweet');
                    })
                    .catch((error) => {alert('')})
            } else if (this.tweetText.length > 140) {
                alert('tweet140');
            } else if(this.tweetText.length === 0) {
                alert('textarea');
            }
        },
        autoTweetOn: async function () {
            const formData = new FormData();
            formData.append('user_id', this.user_id);
            formData.append('screen_name', this.auth_screen_name);

            await this.$axios.post('/api/twitter/autoTweetOn', formData)
                .then((res) => {
                    window.location.reload(false)
                })
                .catch((error) => {alert('')})
        },
        autoTweetStop: async function () {
            const formData = new FormData();
            formData.append('user_id', this.user_id);
            formData.append('screen_name', this.auth_screen_name);

            await this.$axios.post('/api/twitter/autoTweetStop', formData)
                .then((res) => {
                    window.location.reload(false)
                })
                .catch((error) => {alert('')})
        },
    },
}
</script>
